import { defineStore } from "pinia"

export const useTopNav = defineStore('topNav', {
    state: () => {
        return {
            isCollapse:false,
        }
    },
    actions: {
        closeNavbar() {
            if(document.getElementById("mainNavbar").classList.contains("show")) {
                document.getElementById("mainNavbarToggler").click()
            }
        },
        openNavbar() {
            if(!document.getElementById("mainNavbar").classList.contains("show")) {
                document.getElementById("mainNavbarToggler").click()
            }
        },
    }
})