<template>
    <div class="container">
      <div class="row">
        <SideNav/>
        <MainUnit/>
      </div>
    </div>
</template>

<script>
/* eslint-disable */
import { useRoute, useRouter } from 'vue-router'
import SideNav from '@/components/SideNav.vue'
import MainUnit from '@/components/MainUnit.vue'
import { useInitUnit } from "@/store/InitUnit"
import { useWeapon } from "@/store/Weapon"
import { useSkill } from "@/store/Skill"
import { storeToRefs } from 'pinia'

export default {
  name: 'HomeView',
  components: {
      SideNav,
      MainUnit,
  },
  setup() {
    const { allChars, unit } = storeToRefs(useInitUnit())
    const { language } = storeToRefs(useSkill())
    language.value = useRoute().params.language === undefined ? 0 : 1
    if (useRoute().params.heroName === undefined) { // Default Alice
      useInitUnit().setDefault()
      unit.value = allChars.value.chars[0]
      useInitUnit().initialUnit()
      useSkill().setSkill()
      
    }else { // Selected Hero
      const heroName = allChars.value.chars[allChars.value.chars.findIndex(obj => 
        (obj.displayName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase()) || 
        (obj.srcName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase()) || 
        (obj.imgName.toLowerCase() === useRoute().params.heroName.toString().toLowerCase())
      )]
      if (heroName === undefined) {
          useRouter().push({ path:'/' })
      }else{
        useInitUnit().setDefault()
        unit.value = heroName
        useInitUnit().initialUnit()
        useSkill().setSkill()
      }
    } 
  }
}
</script>
