<template>
    <nav class="navbar navbar-expand-lg navbar-light" :class="{ 'fixed-top': isMobile }" id="mainNav" style="background-image: url(img/bg1.png); background-attachment: fixed;">
        <div class="container px-3 px-lg-5 py-2">
            <button type="button" class="btn fs-1" @click="toggleSideNav" v-if="isMobile">
                <i class="bi bi-arrow-bar-left" v-if="isSideNav"></i>
                <i class="bi bi-arrow-bar-right" v-else></i>
            </button>
            <router-link to="/" class="navbar-brand mx-0"><img src="/img/logo_black.png" alt="Grimlight" style="width: 100%;"></router-link>
            <button class="navbar-toggler" id="mainNavbarToggler" type="button" data-bs-toggle="collapse" data-bs-target="#mainNavbar">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="mainNavbar">
                <ul class="navbar-nav ms-auto py-4 py-lg-0" @click="closeNavbar">
                    <router-link to="/"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Heros</a></li></router-link>
                    <router-link to="/haven"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Haven</a></li></router-link>
                    <router-link to="/equipment"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Weapon</a></li></router-link>
                </ul>
            </div>
        </div>
    </nav>
</template>
  
<script>
/* eslint-disable */
import { ref } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useSideNav } from "@/store/SideNav"
import { useTopNav } from '@/store/TopNav'
import { storeToRefs } from 'pinia'
export default {
    name: 'TopNav',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { isSideNav } = storeToRefs(useSideNav())
        const { isCollapse } = storeToRefs(useTopNav())

        const toggleSideNav = () => {
            useSideNav().toggleSideNav()
        }

        const closeNavbar = () => {
            useTopNav().closeNavbar()
        }

        return {
            isMobile,
            isSideNav,
            isCollapse,
            toggleSideNav,
            closeNavbar,
        }
    }
}
</script>

<style scoped>
.navbar-brand {
    width: 15%;
}
.nav-link {
    color: black;
}

@media screen and (max-width: 768px) {
    .navbar-brand {
        width: 25%;
    }
}
</style>